define('first-app/router', ['exports', 'ember', 'first-app/config/environment'], function (exports, Ember, config) {

  'use strict';

  var Router = Ember['default'].Router.extend({
    location: config['default'].locationType
  });

  Router.map(function () {
    this.route('home', {
      path: '/'
    });
    this.route('about', {});
    this.route('examples', {});
  });

  exports['default'] = Router;

});